'use client'

import { useSearchParams } from 'next/navigation'
import { useEffect, useState } from 'react'
import { AnimeCard } from '@/components/anime-card'

interface Anime {
  mal_id: number
  title: string
  episodes?: number
  images: {
    jpg: {
      small_image_url: string
    }
  }
}

export default function SearchPage() {
  const searchParams = useSearchParams()
  const query = searchParams.get('q') || ''
  const [animes, setAnimes] = useState<Anime[]>([])
  const [loading, setLoading] = useState(false)

  useEffect(() => {
    if (!query) return
    const fetchSearch = async () => {
      setLoading(true)
      try {
        const res = await fetch(`/api/search?q=${encodeURIComponent(query)}`)
        if (!res.ok) throw new Error('Failed to fetch search results')
        const data = await res.json()

        // Filter duplikat berdasarkan mal_id
        const uniqueAnimes = data.data.filter(
          (anime: Anime, index: number, self: Anime[]) =>
            self.findIndex(a => a.mal_id === anime.mal_id) === index
        )

        setAnimes(uniqueAnimes)
      } catch (err) {
        console.error('Search failed:', err)
        setAnimes([])
      } finally {
        setLoading(false)
      }
    }

    fetchSearch()
  }, [query])

  return (
    <div className="max-w-7xl mx-auto p-4">
      <h1 className="text-xl font-bold mb-4">Results for "{query}"</h1>

      {loading ? (
        <p>Loading...</p>
      ) : animes.length > 0 ? (
        <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-4 xl:grid-cols-5 gap-4 md:gap-6">
          {animes.map((anime, index) => (
            <AnimeCard key={`${anime.mal_id}-${index}`} anime={anime} />
          ))}
        </div>
      ) : (
        <p>No results found. Try searching with different keywords.</p>
      )}
    </div>
  )
}
